.tj_c{
	overflow:hidden; background-color:#FFF; padding:2% 0;
}
.tj_c ul li{
	width:24.3%; overflow:hidden; float:left; border-left:1px solid #eaeaea; border-right:1px solid #eaeaea;
}
.tj_c ul li .img{
	width:100%; overflow:hidden; margin-top:5px;
}
.tj_c ul li .img img{
	width:100%;
}
.tj_c ul li .name{
	height:40px; line-height:20px; overflow:hidden; font-size:1.3em; text-align:center; margin-top:5px;
}
.tj_c ul li .price{
	height:30px; line-height:30px; overflow:hidden; color:#999999; text-align:center; text-transform:uppercase;
}
.tj_c ul li .price font{
	color:#ff4e00; font-size:1.2em;
}
.tj_c ul li .price span{
	color:#ff4e00; font-size:1.4em;
}
