.fresh_mid{
	overflow:hidden; background-color:#FFF;
}
.fresh_mid ul{
	width:102%; overflow:hidden;
}
.fresh_mid ul li{
	width:33%; overflow:hidden; float:left; border-right:1px solid #eaeaea; border-bottom:1px solid #eaeaea;
}
.fresh_mid ul li .name{
	height:20px; line-height:20px; overflow:hidden; font-size:1.5em; color:#555555; text-align:center; margin-top:4%;
}
.fresh_mid ul li .price{
	height:20px; line-height:20px; overflow:hidden; color:#888888; font-size:1.3em; text-align:center;
}
.fresh_mid ul li .img{
	width:66%; overflow:hidden; margin-top:1%;
}
.fresh_mid ul li .img img{
	width:100%;
}