.i_img{
	width:100%; overflow:hidden; background-color:#FFF; padding:2% 0;
}
.i_img ul{
	width:102%; overflow:hidden;
}
.i_img ul li{
	width:12%; overflow:hidden; text-align:center; font-size:1.4em; line-height:1.3em; float:left; margin:2% 6%;
}
.i_img ul li img{
	width:100%; margin:0 auto 10px auto;
}